import { useEffect, useState } from "react";
import Footer from "../components/footer/Footer"
import DbotCity from "../components/vectors/DbotCity";
import DbotOnline from "../components/vectors/DbotOnline";
import MobileLogo from "../components/vectors/MobileLogo"

const Home = () => {
    const [width, setWidth] = useState(window.innerWidth);
    const handleResize = (e) => setWidth(window.innerWidth)
    useEffect(() => {
        window.addEventListener('resize', handleResize)
        return () => {
            window.removeEventListener('resize', handleResize)
        };
    }, []);

    return (
        <div className="min-h-full">
            <section className="mb-4 p-4">
                <div className="block md:flex items-center justify-center">
                    <div className="w-full md:w-5/12 my-2">
                        <span className="text-center text-6xl block">D-Bot</span>
                        <span className="text-center text-xl block">Una solución para automatizar</span>
                        <span className="text-center text-4xl block text-blue-700">Todo servicio a un mensaje de distancia</span>
                    </div>
                    <div className="flex justify-center md:w-5/12">
                        <MobileLogo />
                    </div>
                </div>
            </section>
            <section className="bg-blue-700 p-12 text-white font-light" style={{ clipPath: 'polygon(0 0, 100% 10%, 100% 100%, 0 90%)' }}>
                <h1 className="text-2xl md:text-4xl max-w-4xl m-auto">D-bot es un bot que permite el acceso a productos y servicios de manera rápida y eficiente.</h1>
            </section>

            <section className="p-12 ">
                <div className="m-auto flex justify-center my-4">
                    <DbotOnline width={width / 1.2} />
                </div>
                <h1 className="text-lg md:text-2xl max-w-4xl m-auto pt-4">Para ello utiliza inteligencia artificial (IA), está analiza tu solicitud y el contexto para hacerte llegar lo que necesitas exactamente.
                </h1>
            </section>

            <section className="p-12 bg-blue-700 text-white font-light" style={{ clipPath: "polygon(0 5%, 100% 0, 100% 95%, 0% 100%)" }}>
                <h1 className="text-lg md:text-2xl max-w-4xl m-auto">La manera de utilizarlo es simple, entrenas a la IA con la información de tu negocio y ella será capaz de atender a todos los clientes de manera automática a través de Whatsapp.
                </h1>
            </section>

            <section className="p-12 ">
                <div className="m-auto flex justify-center my-4 p-4 rounded-2xl bg-trueGray-500 shadow-lg">
                    <DbotCity width={width / 1.2} />
                </div>
                <h1 className="text-lg md:text-2xl max-w-4xl m-auto pt-4">En maracaibo innova fue implementado a los servicios de emergencia del estado (911) para así poder acceder a ellos fácilmente y sin intermediarios.</h1>
            </section>

            <section className="p-12 bg-blue-700 text-white font-light">
                <h1 className="text-lg md:text-2xl max-w-4xl m-auto">Esto te permite obtener los servicios (pe. ambulancia, bomberos, policía) sin tener que realizar llamadas que en ocasiones tardan más de lo necesario, y es tiempo que no se puede perder en caso de emergencias.</h1>
            </section>

            <section className="p-12 text-center    ">
                <h1 className="text-4xl max-w-4xl m-auto">Gracias a D-bot cualquier servicio está disponible a un <span className="text-blue-700 font-semibold">mensaje de distancia</span>.</h1>
            </section>
            <Footer />
        </div>
    );
}

export default Home;
